body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.header{
  background-color: black;
  color:white;
  padding:20px 70px;
  display: flex;
  justify-content: space-between;
}
.cart-icon .counter{
  height:18px;
  width:18px;
  background-color: blue;
  border-radius: 50%;
  text-align: center;
  position: relative;
  right:7px;
  top:-6px;
}
.cart-icon{
  display: flex;
  text-align: center;
}
.icon{
  position: relative;
right: 2px;
font-size: 22px;


}
img{
  height:150px;
  
}
.card-wrapper{
  display: inline-flex;
  margin: 40px;
  box-shadow: 0px 0px 7px black;
  width:320px;
  border-radius: 5px;

}
.image-wrapper{
  width:180px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding:10px;
  border-right:1px solid rgb(201,201,201);
}
.content{
  flex-grow:1;
  display: flex;
  flex-direction: column;
  gap:10px;
  padding:10px;
  text-align: center;
  align-items: center;
  justify-content: center;
}
.price{
  margin-bottom: auto;
}
.content button {
  background-color: black;
  color: white;
  border-radius: 3px;
}
.quantity{
  display:flex;
  align-self: flex-end;
  border-radius: 3px;
  border:1px solid grey;

 
}

.quantity div{
  height:22px;
  width:22px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor:pointer;
}
.quantity>div:first-child ,
.quantity>div:last-child{
 background-color: rgb(0,0,111);
 color:white;

}
main{
  padding:20px;
  display:flex;
  flex-wrap:wrap;
  min-height: 80vh;
  justify-content: center;
  align-items: center;
  align-content: center;
  gap:50px;
}
.total{
  box-sizing: border-box;
  background-color: grey;
  padding:13px;
  bottom:0;
  padding-right: 30px;
  position: fixed;
  width: 100%;
  text-align: right;
}
.title{
  font-size: 20px;
  font-family:Verdana, Geneva, Tahoma, sans-serif;
}
